@import url(https://fonts.googleapis.com/css2?family=Lato:wght@300&display=swap);
@import url(https://cdnjs.cloudflare.com/ajax/libs/font-awesome/5.15.1/css/all.min.css);
:root {
  --primary-color: #047aed;
  --secondary-color: #1c3fa8;
  --dark-color: #002240;
  --light-color: #f4f4f4;
  --success-color: #5cb85c;
  --error-color: #d9534f;
}

* { 
   scroll-behavior: smooth; /* chrome */
  -webkit-overflow-scrolling: touch; /* safari */
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

body {
  font-family: "Lato", sans-serif;
  color: #333;
  line-height: 1.6;
}

ul {
  list-style-type: none;
}

a {
  text-decoration: none;
  color: #333;
}

h1,
h2 {
  font-weight: 300;
  line-height: 1.2;
  margin: 10px 0;
}

p {
  margin: 10px 0;
}

img {
  width: 100%;
}

code,
pre {
  background-color: #333;
  color: #fff;
  padding: 10px;
}

.hidden {
  visibility: hidden;
  height: 0;
}

/* navbar */
.navbar {
  position: fixed;
  z-index: 2;
  top: 0;
  width: 100%;
  background-color: #047aed;
  background-color: var(--primary-color);
  color: #fff;
  height: 70px;
}

.showcase , .languages , .exprerience , .hobbies{
  padding-top: 9.6vh;
}

.navbar ul {
  display: flex;
  justify-content: center;
  width: 100%;
}

.navbar a {
  color: #fff;
  padding: 10px;
  margin: 0 5px;
}

.navbar a:hover {
  border-bottom: 2px #fff solid;
}

.navbar .flex {
  justify-content: space-between;
}

/* Showcase */
.showcase {
  height: 400px;
  background-color: #047aed;
  background-color: var(--primary-color);
  color: #fff;
  position: relative;
}

.showcase h1 {
  font-size: 40px;
}

.showcase p {
  margin: 20px 0;
}

.showcase .grid {
  overflow: visible;
  grid-template-columns: 55% auto;
  grid-gap: 30px;
  gap: 30px;
}

.showcase-form {
  position: relative;
  top: 60px;
  /* height: 350px; */
  width: 400px;
  padding: 40px;
  z-index: 1;
  justify-content: flex-end;
}

.showcase-form .form-control {
  margin: 15px 0;
}

/* .showcase-form .form-control {
  display: inline-grid;
  justify-content: center;
  align-items: center;
} */

.showcase::before,
.showcase::after {
  content: "";
  position: absolute;
  height: 100px;
  bottom: -70px;
  right: 0;
  left: 0;
  background: #fff;
  transform: skewY(-2deg);
}

/* Skill Languages */

.languages .flex {
  flex-wrap: wrap;
  overflow: hidden;
}

.languages .card {
  text-align: center;
  margin: 0.1rem;
}

.languages .card h4 {
  font-size: 1rem;
  margin-bottom: 0.5em;
}

/* .languages .card:hover {
  transform: scale(1.1);
  transition: transform 0.2s ease-in;
} */

/* Hobbies */

.hobbies .card > i {
  margin-right: 20px;
}

.hobbies .grid {
  padding: 1.87rem;
  /* margin: 1rem; */
  overflow: hidden;
}

.hobbies .grid > *:first-child {
  grid-column: 1 / span 3;
}

.hobbies .grid > *:nth-child(2) {
  grid-column: 1 / span 2;
}

/* Footer */
.footer .social a {
  margin: 0 10px;
}

/* tablet */
@media (max-width: 768px) {
  .grid,
  .showcase .grid,
  .languages .grid,
  .hobbies .grid {
    grid-template-columns: 1fr;
    grid-template-rows: 1fr;
    grid-gap: 0.5rem;
    gap: 0.5rem;
  }

  .languages .grid{
    grid-template-columns: repeat(3 ,1fr) !important;
  }

  .showcase {
    height: auto;
  }
  .showcase-form {
    justify-self: center;
    margin: auto;
  }

  .hobbies {
    text-align: center;
  }

  .hobbies img {
    justify-self: center;
  }

  .hobbies .grid > *:first-child,
  .hobbies .grid > *:nth-child(2) {
    grid-column: 1;
  }

  .form-control .flex{
    flex-direction: column;
    flex-wrap: wrap;
  }
}


/* Mobile */

@media (max-width: 500px){

 .languages .grid{
    grid-template-columns: repeat(2 ,1fr) !important;
  }

  /* .navbar{
    height: 110px;
  } */

  .navbar .flex{
    flex-direction: column;
  }

  .navbar ul{
    padding: 10px;
    background-color: rgba(0, 0,0, 0.1);
  }

  .showcase-form{
    width: 300px;
  }

  .flex{
    flex-direction: column;
    flex-wrap: wrap;
  }

  .showcase , .languages , .exprerience , .hobbies{
    padding-top: 5vh;
  }
}

/* The actual timeline (the vertical ruler) */
.timeline {
  position: relative;
  max-width: 1200px;
  margin: 0 auto;
}

/* The actual timeline (the vertical ruler) */
.timeline::after {
  content: '';
  position: absolute;
  width: 6px;
  background-color: white;
  top: 0;
  bottom: 0;
  left: 50%;
  margin-left: -3px;
}

/* Container around content */
.container-timeline {
  padding: 10px 40px;
  position: relative;
  background-color: inherit;
  width: 50%;
}

/* The circles on the timeline */
.container-timeline::after {
  content: '';
  position: absolute;
  width: 25px;
  height: 25px;
  right: -17px;
  background-color: white;
  border: 4px solid #cec6c0;
  top: 15px;
  border-radius: 50%;
  z-index: 1;
}

/* Place the container to the left */
.left {
  left: 0;
}

/* Place the container to the right */
.right {
  left: 50%;
}

/* Add arrows to the left container (pointing right) */
.left::before {
  content: " ";
  height: 0;
  position: absolute;
  top: 22px;
  width: 0;
  z-index: 1;
  right: 30px;
  border: medium solid white;
  border-width: 10px 0 10px 10px;
  border-color: transparent transparent transparent white;
}

/* Add arrows to the right container (pointing left) */
.right::before {
  content: " ";
  height: 0;
  position: absolute;
  top: 22px;
  width: 0;
  z-index: 1;
  left: 30px;
  border: medium solid white;
  border-width: 10px 10px 10px 0;
  border-color: transparent white transparent transparent;
}

/* Fix the circle for containers on the right side */
.right::after {
  left: -16px;
}

/* The actual content */
.content {
  padding: 20px 30px;
  background-color: white;
  position: relative;
  border-radius: 6px;
}

/* Media queries - Responsive timeline on screens less than 600px wide */
@media screen and (max-width: 600px) {
  /* Place the timelime to the left */
  .timeline::after {
  left: 31px;
  }
  
  /* Full-width containers */
  .container-timeline {
  width: 100%;
  padding-left: 70px;
  padding-right: 25px;
  }
  
  /* Make sure that all arrows are pointing leftwards */
  .container-timeline::before {
  left: 60px;
  border: medium solid white;
  border-width: 10px 10px 10px 0;
  border-color: transparent white transparent transparent;
  }

  /* Make sure all circles are at the same spot */
  .left::after, .right::after {
  left: 15px;
  }
  
  /* Make all right containers behave like the left ones */
  .right {
  left: 0%;
  }
}
.container {
  max-width: 1100px;
  margin: 0 auto;
  overflow: auto;
  padding: 0 2.5rem;
}

section{
  margin-top: 1em;
}

.card {
  background-color: #fff;
  color: #333;
  border-radius: 0.6em;
  box-shadow: 0 3px 0.6em rgba(0, 0, 0, 0.2);
  padding: 1em;
  margin: 1em;
}

.btn {
  display: inline-block;
  padding: 0.6rem 1.8rem;
  cursor: pointer;
  background: var(--primary-color);
  color: #fff;
  border: none;
  border-radius: 5px;
}

.btn-outline {
  background: transparent;
  border: 1px #fff solid;
}

.btn:hover {
  transform: scale(0.98);
}

.bg-primary,
.btn-primary {
  background-color: var(--primary-color);
}

.bg-dark,
.btn-dark {
  background-color: var(--dark-color);
  color: #fff;
}

.bg-light,
.btn-light {
  background-color: var(--light-color);
  color: #333;
}

.bg-primary a,
.btn-primary a,
.bg-secondary a,
.btn-secondary a,
.bg-dark a,
.btn-dark a {
  color: #fff;
}

.text-primary {
  color: var(--primary-color);
}

.text-secondary {
  color: var(--secondary-color);
}

.text-dark {
  color: var(--dark-color);
}

.text-light {
  color: var(--light-color);
}

.lead {
  font-size: 1.25rem;
}

.underline
{
  padding-bottom: 1rem;
  border-bottom: solid 1px var(--primary-color);
}

.sm {
  font-size: 1rem;
}

.md {
  font-size: 2rem;
}

.lg {
  font-size: 3rem;
}

.xl {
  font-size: 4rem;
}

.text-center {
  text-align: center;
}

.text-weight-bold {
  font-weight: bolder;
}


.alert {
  background-color: var(--light-color);
  padding: 0.6rem 1.25rem;
  font-weight: bold;
  margin: 15px 0;
}

.alert i {
  margin-right: 0.6rem;
}

.alert-success {
  background-color: var(--success-color);
  color: #fff;
}

.alert-error {
  background-color: var(--error-color);
  color: #fff;
}

.flex {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
}

.flex-space-between{
  justify-content: space-between;
}

.grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 1rem;
  gap: 1rem;
  justify-content: center;
  align-items: center;
